* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.page {
    margin: 0 8%;
    margin-top: 2.5%;
    height: auto;
    font-size: 1.05rem;
    text-align: justify;
}
.page h1 {
    margin-bottom: 1rem;
    margin-top: 0rem;
    text-align: center;
}

.activePage {
    padding-left: 280px;
}
@media screen and (max-width: 500px) {
  .page{
        margin: 0 6%;
        font-size: 0.9rem;
    }
}
